
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import Link from "next/link";
import { ArrowRight, ShoppingBag, MessageSquare, Users, BarChart3, PieChartIcon, LineChartIcon } from "lucide-react";
import { services, testimonials, clientInquiries, type ClientInquiryStatus } from "@/lib/placeholder-data";
import {
  ChartContainer,
  ChartTooltip,
  ChartTooltipContent,
  ChartLegend,
  ChartLegendContent,
  type ChartConfig
} from "@/components/ui/chart";
import { Bar, BarChart, Pie, PieChart, Line, LineChart, CartesianGrid, XAxis, YAxis, Tooltip as RechartsTooltip } from "recharts";
import { format, parseISO, startOfMonth, compareAsc } from 'date-fns';

export default function AdminDashboardPage() {
  const stats = [
    { title: "Total Services", value: services.length, icon: ShoppingBag, href: "/admin/services" },
    { title: "Total Testimonials", value: testimonials.length, icon: Users, href: "/admin/testimonials" },
    { title: "New Inquiries", value: clientInquiries.filter(inq => inq.status === 'New').length, icon: MessageSquare, href: "/admin/inquiries" },
    { title: "Total Inquiries", value: clientInquiries.length, icon: MessageSquare, href: "/admin/inquiries" },
  ];

  // Data for "Service Request Distribution" chart
  const serviceRequestCounts: { [key: string]: number } = {};
  clientInquiries.forEach(inquiry => {
    serviceRequestCounts[inquiry.serviceRequested] = (serviceRequestCounts[inquiry.serviceRequested] || 0) + 1;
  });
  const serviceRequestChartData = Object.entries(serviceRequestCounts)
    .map(([name, count]) => ({ name, count, fill: `var(--chart-${(Object.keys(serviceRequestCounts).indexOf(name) % 5) + 1})` }))
    .sort((a, b) => b.count - a.count); // Sort by count descending

  const serviceRequestChartConfig = serviceRequestChartData.reduce((acc, item, index) => {
    acc[item.name] = {
      label: item.name,
      color: `hsl(var(--chart-${(index % 5) + 1}))`,
    };
    return acc;
  }, {} as ChartConfig);


  // Data for "Inquiry Status Overview" chart
  const statusCounts: { [key in ClientInquiryStatus]: number } = {
    New: 0,
    Contacted: 0,
    Quoted: 0,
    Closed: 0,
  };
  clientInquiries.forEach(inquiry => {
    statusCounts[inquiry.status] = (statusCounts[inquiry.status] || 0) + 1;
  });

  const statusChartData = Object.entries(statusCounts).map(([name, value], index) => ({
    status: name,
    count: value,
    fill: `var(--chart-${(index % 5) + 1})`,
  }));

  const statusChartConfig = {
    New: { label: "New", color: "hsl(var(--chart-1))" },
    Contacted: { label: "Contacted", color: "hsl(var(--chart-2))" },
    Quoted: { label: "Quoted", color: "hsl(var(--chart-3))" },
    Closed: { label: "Closed", color: "hsl(var(--chart-4))" },
  } satisfies ChartConfig;


  // Data for "Inquiries Over Time (Monthly)" chart
  const inquiriesByMonth: { [key: string]: { date: Date, count: number } } = {};
  clientInquiries.forEach(inquiry => {
    try {
      const inquiryDate = parseISO(inquiry.date);
      const monthKey = format(startOfMonth(inquiryDate), 'yyyy-MM-dd'); // Use ISO for easier sorting
      
      if (!inquiriesByMonth[monthKey]) {
        inquiriesByMonth[monthKey] = { date: startOfMonth(inquiryDate), count: 0 };
      }
      inquiriesByMonth[monthKey].count += 1;
    } catch (e) {
      console.warn(`Could not parse date for inquiry ${inquiry.id}: ${inquiry.date}`, e);
    }
  });
  
  const inquiriesTimeChartData = Object.values(inquiriesByMonth)
    .sort((a, b) => compareAsc(a.date, b.date)) // Sort by date ascending
    .map(item => ({
      month: format(item.date, 'MMM yyyy'),
      count: item.count,
    }));
    
  const inquiriesTimeChartConfig = {
    count: {
      label: "Inquiries",
      color: "hsl(var(--chart-1))",
    },
  } satisfies ChartConfig;


  return (
    <div className="space-y-8">
      <h1 className="text-3xl font-bold text-primary">Admin Dashboard</h1>

      <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-4">
        {stats.map((stat) => (
          <Card key={stat.title} className="shadow-lg">
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                {stat.title}
              </CardTitle>
              <stat.icon className="h-5 w-5 text-muted-foreground" />
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold text-primary">{stat.value}</div>
              <Button variant="link" asChild className="px-0 text-sm text-accent hover:text-accent/80">
                <Link href={stat.href}>
                  View Details <ArrowRight className="ml-1 h-4 w-4" />
                </Link>
              </Button>
            </CardContent>
          </Card>
        ))}
      </div>

      <div className="grid gap-6 md:grid-cols-1 lg:grid-cols-2">
        <Card className="shadow-lg">
          <CardHeader>
            <CardTitle className="text-xl text-primary flex items-center gap-2">
              <BarChart3 className="h-6 w-6" /> Service Request Distribution
            </CardTitle>
            <CardDescription>Number of inquiries per service requested.</CardDescription>
          </CardHeader>
          <CardContent>
            {serviceRequestChartData.length > 0 ? (
              <ChartContainer config={serviceRequestChartConfig} className="min-h-[250px] w-full">
                <BarChart accessibilityLayer data={serviceRequestChartData} layout="vertical" margin={{left: 20, right: 20}}>
                  <CartesianGrid vertical={true} horizontal={false} />
                  <XAxis type="number" dataKey="count" />
                  <YAxis dataKey="name" type="category" tickLine={false} axisLine={false} strokeWidth={1} width={120} />
                  <ChartTooltip content={<ChartTooltipContent hideLabel />} />
                  <Bar dataKey="count" radius={5}>
                     {serviceRequestChartData.map((entry) => (
                        <svg key={entry.name}><rect fill={entry.fill} /></svg> // Minimal way to use Cell's fill in Bar
                      ))}
                  </Bar>
                </BarChart>
              </ChartContainer>
            ) : (
              <p className="text-muted-foreground text-center py-4">No inquiry data to display service distribution.</p>
            )}
          </CardContent>
        </Card>

        <Card className="shadow-lg">
          <CardHeader>
            <CardTitle className="text-xl text-primary flex items-center gap-2">
              <PieChartIcon className="h-6 w-6" /> Inquiry Status Overview
            </CardTitle>
            <CardDescription>Distribution of inquiries by current status.</CardDescription>
          </CardHeader>
          <CardContent className="flex items-center justify-center">
            {clientInquiries.length > 0 ? (
              <ChartContainer config={statusChartConfig} className="min-h-[250px] w-full max-w-[300px] aspect-square">
                <PieChart>
                  <ChartTooltip content={<ChartTooltipContent nameKey="count" hideLabel />} />
                  <Pie data={statusChartData} dataKey="count" nameKey="status" innerRadius={60} outerRadius={90} paddingAngle={2}>
                    {statusChartData.map((entry) => (
                      <svg key={entry.status}><rect fill={entry.fill} /></svg> // Minimal way to use Cell's fill in Pie
                    ))}
                  </Pie>
                  <ChartLegend content={<ChartLegendContent nameKey="status" />} className="-translate-y-2 flex-wrap gap-2 [&>*]:basis-1/4 [&>*]:justify-center" />
                </PieChart>
              </ChartContainer>
            ) : (
              <p className="text-muted-foreground text-center py-4">No inquiry data to display status overview.</p>
            )}
          </CardContent>
        </Card>
      </div>
      
      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle className="text-xl text-primary flex items-center gap-2">
            <LineChartIcon className="h-6 w-6" /> Inquiries Over Time
          </CardTitle>
          <CardDescription>Number of inquiries received per month.</CardDescription>
        </CardHeader>
        <CardContent>
          {inquiriesTimeChartData.length > 0 ? (
            <ChartContainer config={inquiriesTimeChartConfig} className="min-h-[300px] w-full">
              <LineChart
                accessibilityLayer
                data={inquiriesTimeChartData}
                margin={{
                  left: 12,
                  right: 12,
                }}
              >
                <CartesianGrid vertical={false} />
                <XAxis
                  dataKey="month"
                  tickLine={false}
                  axisLine={false}
                  tickMargin={8}
                  tickFormatter={(value) => value.slice(0, 3)} // Abbreviate month
                />
                <YAxis allowDecimals={false} />
                <ChartTooltip cursor={false} content={<ChartTooltipContent indicator="line" />} />
                <Line
                  dataKey="count"
                  type="natural"
                  stroke="var(--color-count)"
                  strokeWidth={2}
                  dot={{
                    fill: "var(--color-count)",
                  }}
                  activeDot={{
                    r: 6,
                  }}
                />
              </LineChart>
            </ChartContainer>
          ) : (
            <p className="text-muted-foreground text-center py-4">No inquiry data to display trend over time.</p>
          )}
        </CardContent>
      </Card>

      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle className="text-xl text-primary">Recent Inquiries</CardTitle>
        </CardHeader>
        <CardContent>
          {clientInquiries.slice(0, 5).map((inquiry) => (
            <div key={inquiry.id} className="flex items-center justify-between py-3 border-b last:border-b-0">
              <div>
                <p className="font-medium">{inquiry.name} <span className="text-xs text-muted-foreground">({inquiry.serviceRequested})</span></p>
                <p className="text-sm text-muted-foreground">{inquiry.email}</p>
              </div>
              <Button variant="outline" size="sm" asChild>
                <Link href={`/admin/inquiries?id=${inquiry.id}`}>View Details</Link>
              </Button>
            </div>
          ))}
          {clientInquiries.length === 0 && (
            <p className="text-muted-foreground">No recent inquiries.</p>
          )}
           <div className="mt-4 text-center">
             <Button variant="default" asChild>
                <Link href="/admin/inquiries">View All Inquiries</Link>
             </Button>
           </div>
        </CardContent>
      </Card>
    </div>
  );
}

